import "./styles.scss";

import anime from "animejs";

const width = 1080;
const height = 1920;

let screenWidth = 0;
let screenHeight = 0;

const resize = () => {
  const main = document.getElementById("main");
  if (main) {
    const mainBR = main.getBoundingClientRect();
    const mainWidth = mainBR.width;
    const mainHeight = mainBR.height;
    let scaledWidth = 0;
    let scaledHeight = 0;
    if (mainWidth / mainHeight < width / height) {
      scaledWidth = mainWidth;
      scaledHeight = (mainWidth * height) / width;
    } else {
      scaledWidth = (mainHeight * width) / height;
      scaledHeight = mainHeight;
    }
    document.getElementById(
      "screen"
    ).style = `width: ${scaledWidth}px; height: ${scaledHeight}px`;
    screenHeight = scaledHeight;
    screenWidth = scaledWidth;
  }
};

window.addEventListener("resize", resize);
resize();

var ml4 = {};
ml4.opacityIn = [0, 1];
ml4.scaleIn = [0.2, 1];
ml4.scaleOut = 3;
ml4.durationIn = 800;
ml4.durationOut = 600;
ml4.delay = 500;

const animation = anime
  .timeline({ loop: true })
  .add({
    targets: ".letters-1",
    opacity: ml4.opacityIn,
    scale: ml4.scaleIn,
    duration: ml4.durationIn
  })
  .add({
    targets: ".letters-1",
    opacity: 0,
    scale: ml4.scaleOut,
    duration: ml4.durationOut,
    easing: "easeInExpo",
    delay: ml4.delay
  })
  .add({
    targets: ".letters-2",
    opacity: ml4.opacityIn,
    scale: ml4.scaleIn,
    duration: ml4.durationIn
  })
  .add({
    targets: ".letters-2",
    opacity: 0,
    scale: ml4.scaleOut,
    duration: ml4.durationOut,
    easing: "easeInExpo",
    delay: ml4.delay
  })
  .add({
    targets: ".letters-3",
    opacity: ml4.opacityIn,
    scale: ml4.scaleIn,
    duration: ml4.durationIn
  })
  .add({
    targets: ".letters-3",
    opacity: 0,
    scale: ml4.scaleOut,
    duration: ml4.durationOut,
    easing: "easeInExpo",
    delay: ml4.delay
  });

const play = () => {
  animation.play();
};
const seek = (frame) => {
  animation.seek(frame);
};
const pause = () => {
  animation.pause();
};
const stop = () => {
  animation.pause();
  animation.seek(0);
};

//stop();

window.addEventListener("message", function (event) {
  const { frame, action } = event.data;
  if (frame) {
    seek(frame);
  }
  if (action === "play") {
    play();
  }
  if (action === "pause") {
    pause();
  }
  if (action === "stop") {
    stop();
  }
});
